
























import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class NavigationDrawerComponent extends Vue {
  @Prop({ default: false, required: true })
  drawer!: boolean;

  private scrollToProjects() {
    this.$emit("scrollToProjects");
  }

  private scrollToHome() {
    this.$emit("scrollToHome");
  }

  private scrollToContact() {
    this.$emit("scrollToContact");
  }
}
