import ProjectModel from "@/models/Project.model";
import NavigationDrawerComponent from "@/components/NavigationDrawer.component.vue";
import AppBarComponent from "@/components/AppBarComponent.vue";
import { Keyword } from "@/enums/Keyword.enum";
import LinkModel from "@/models/Link.model";

@Component({
  components: {
    AppBarComponent,
    NavigationDrawerComponent,
  },
})
export default class MainView extends Vue {
  currentPage = 0;
  drawer = false;
  snackbar = false;
  profilePicture = require("../assets/misc/profile.png");

  contactAnimationJson = require("../assets/animations/contactAnimation.json");
  profileAnimationJson = require("../assets/animations/profileAnimation.json");
  likeAnimationJson = require("../assets/animations/likeAnimation.json");

  likeAnimation: any;

  length = 3;

  filterKeywords: number[] = [];
  body = "";

  languages: string[] = [
    this.$t("TECHNOLOGIES.FLUTTER").toString(),
    this.$t("TECHNOLOGIES.VUE").toString(),
    this.$t("TECHNOLOGIES.TYPESCRIPT").toString(),
    this.$t("TECHNOLOGIES.KOTLIN").toString(),
    this.$t("TECHNOLOGIES.UNITY").toString(),
    this.$t("TECHNOLOGIES.ANDROID_COMPOSE").toString(),
  ];

  mounted() {
    const profileAnimationHandler: any =
      document.getElementById("profile-animation");
    profileAnimationHandler.addEventListener("rendered", (_: any) => {
      profileAnimationHandler.load(this.profileAnimationJson);
    });

    const contactAnimationHandler: any =
      document.getElementById("contact-animation");
    contactAnimationHandler.addEventListener("rendered", (_: any) => {
      contactAnimationHandler.load(this.contactAnimationJson);
    });

    const likeAnimationHandler: any = document.getElementById("like-animation");
    likeAnimationHandler.addEventListener("rendered", (_: any) => {
      this.likeAnimation = likeAnimationHandler.load(this.likeAnimationJson);
    });
  }

  private copyMailToClipboard() {
    navigator.clipboard.writeText("werdermann.lukas@gmail.com");
    this.snackbar = true;
  }

  private calculateProjects(): Partial<ProjectModel>[][] {
    let projectPages: Partial<ProjectModel>[][] = [];

    for (let i = 0; i < this.projectPages; i++) {
      projectPages.push([]);
    }

    let counter = 0;
    let iterator = 1;

    for (let i = 0; i < this.filteredProjects.length; i++) {
      if (iterator < this.projectsPerBreakpoint) {
        projectPages[counter].push(this.filteredProjects[i]);
        iterator++;
      } else {
        projectPages[counter].push(this.filteredProjects[i]);
        counter++;
        iterator = 1;
      }
    }

    return projectPages;
  }

  private get projectPages() {
    return Math.ceil(this.filteredProjects.length / this.projectsPerBreakpoint);
  }

  private get projectsPerBreakpoint(): number {
    if (this.$vuetify.breakpoint.xl) {
      return 4;
    } else if (this.$vuetify.breakpoint.lg) {
      return 3;
    } else if (this.$vuetify.breakpoint.md) {
      return 2;
    } else {
      return 1;
    }
  }

  private links: LinkModel[] = [
    {
      icon: "mdi-github",
      link: "https://github.com/werdermann",
    },
    {
      icon: "mdi-gitlab",
      link: "https://gitlab.com/werdermann",
    },
    {
      icon: "mdi-linkedin",
      link: "https://www.linkedin.com/in/lukas-werdermann-0b77ab229/",
    },
  ];

  private allProjects: Partial<ProjectModel>[] = [
    {
      name: "Sterneneltern Achim",
      description:
        "Forum and info app for people who experienced child lose (star child), coworkers and people who want to exchange information about this topic",
      imageSource: require("../assets/projects/stars.jpg"),
      keywords: [Keyword.FORUM, Keyword.FLUTTER, Keyword.APP],
      iosLink:
        "https://apps.apple.com/de/app/sterneneltern-achim/id1588257193?platform=iphone",
      androidLink:
        "https://play.google.com/store/apps/details?id=de.sterneneltern_achim.sterneneltern_achim&gl=DE",
    },
    {
      name: "HofficeDude",
      description:
        "Dashboard for your HofficeDude-Cube that allows to inform your coworkers about your current working status at home to keep work engagement high",
      imageSource: require("../assets/projects/HofficeDude.png"),
      keywords: [Keyword.DASHBOARD, Keyword.HOME_OFFICE, Keyword.VUE],
      webLink: "https://office-dude.dev.28apps-software.de/",
    },
    {
      name: "Biodiversity Warehouse",
      description:
        "[STUDENT PROJECT] Save animals that you saw inside a biodiversity atlas to keep record of the local animals in your area by taking photos",
      imageSource: require("../assets/projects/biodiversity.jpg"),
      keywords: [Keyword.APP, Keyword.FLUTTER, Keyword.BLOC],
      iosLink:
        "https://apps.apple.com/de/app/biodiversity-warehouse/id1540739949?platform=iphone",
      androidLink:
        "https://play.google.com/store/apps/details?id=de.hsb.biodiversity_v4&hl=en_US&gl=US",
    },
  ];

  private next() {
    this.currentPage =
      this.currentPage + 1 === this.projectPages ? 0 : this.currentPage + 1;
  }

  private prev() {
    this.currentPage =
      this.currentPage - 1 < 0 ? this.projectPages - 1 : this.currentPage - 1;
  }

  private openUrl(url: string): void {
    window.open(url);
  }

  private sendMail(): void {
    window.open(`mailto:werdermann.lukas@gmail.com?body=${this.body}`);
  }

  /**
   * Function for scrolling to the select section
   * @param elementId
   */
  private scrollTo(elementId: string): void {
    // Hide drawer
    this.drawer = false;

    // Scroll to selected position
    let element: HTMLElement | null = document.getElementById(elementId);
    element?.scrollIntoView({ behavior: "smooth", block: "start" });
  }

  private get filteredProjects() {
    const filteredResults: Partial<ProjectModel>[] = [];

    this.allProjects.forEach((project) => {
      // Return every project if no keywords are selected
      if (this.filterKeywords.length === 0) {
        filteredResults.push(project);
        return;
      }

      let filterCount = 0;

      project.keywords?.forEach((keyword: Keyword) => {
        if (this.filterKeywords.includes(keyword)) {
          filterCount++;
        }
      });

      if (filterCount === this.filterKeywords.length) {
        filteredResults.push(project);
        return;
      }
    });
    return filteredResults;
  }

  private openFirstAvailableLink(project: Partial<ProjectModel>) {
    if (project.iosLink) {
      window.open(project.iosLink);
    } else if (project.androidLink) {
      window.open(project.androidLink);
    } else {
      window.open(project.webLink);
    }
  }

  private keywordToTranslatedText(keyword: Keyword): string {
    switch (keyword) {
      case Keyword.APP: {
        return this.$t("KEYWORDS.APP").toString();
      }
      case Keyword.BLOC: {
        return this.$t("KEYWORDS.BLOC").toString();
      }
      case Keyword.DASHBOARD: {
        return this.$t("KEYWORDS.DASHBOARD").toString();
      }
      case Keyword.FLUTTER: {
        return this.$t("KEYWORDS.FLUTTER").toString();
      }
      case Keyword.FORUM: {
        return this.$t("KEYWORDS.FORUM").toString();
      }
      case Keyword.HOME_OFFICE: {
        return this.$t("KEYWORDS.HOME_OFFICE").toString();
      }
      case Keyword.VUE: {
        return this.$t("KEYWORDS.VUE").toString();
      }
    }
  }

  private resetFiltering() {
    this.filterKeywords = [];
  }

  private iOS() {
    return (
      [
        "iPad Simulator",
        "iPhone Simulator",
        "iPod Simulator",
        "iPad",
        "iPhone",
        "iPod",
      ].includes(navigator.platform) ||
      // iPad on iOS 13 detection
      (navigator.userAgent.includes("Mac") && "ontouchend" in document)
    );
  }
}
